  font-family: "Noto Sans KR", sans-serif;
  font-weight: 300;
  padding-left: 25px;
  font-size: 13px;

  border-radius: 7px;
}

.about__walet > h5 {
  all: unset;
  margin-bottom: 10px;
  font-size: 20px;
}

.walet {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  margin-left: 7%;
  width: 90%;
  height: 50px;
  border: 2px solid #ebebeb;
  font-family: "Noto Sans KR", sans-serif;
  font-weight: 300;
  padding-left: 25px;
  font-size: 13px;

  border-radius: 7px;
}

.walet > h3 {
  all: unset;
  padding-right: 20px;
}

.about__icon {
  display: flex;
  flex-direction: column;
  cursor: pointer;
  font-family: "Noto Sans KR", sans-serif;
  font-weight: 300;
  margin-left: 1%;
  margin-top: 2%;
  align-items: center;
}

.one__tool {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  align-items: center;
  width: 82%;
}

.two__tool {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 83%;
}

.icon__tool {
  display: flex;

  flex-direction: column;
  align-items: center;
  margin-bottom: 50px;
}

.item-1,
.item-2,
.item-3 {
  position: absolute;
  display: block;

  margin-left: 7%;
  width: 60%;
  font-weight: 300;
  font-size: 15px;
  font-family: "Noto Sans KR", sans-serif;
  animation-duration: 6s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
  cursor: pointer;
}

.item-1 {
  animation-name: anim-1;
}

.item-2 {
  animation-name: anim-2;
}

.item-3 {
  animation-name: anim-3;
}

@keyframes anim-1 {
  0%,
  8.3% {
    top: 82%;
    opacity: 0;
  }
  8.3%,
  25% {
    top: 80%;
    opacity: 1;
  }
  33.33%,
  100% {
    top: 78%;
    opacity: 0;
  }
}

@keyframes anim-2 {
  0%,
  33.33% {
    top: 82%;
    opacity: 0;
  }
  41.63%,
  58.29% {
    top: 80%;
    opacity: 1;
  }
  66.66%,
  100% {
    top: 78%;
    opacity: 0;
  }
}

@keyframes anim-3 {
  0%,
  66.66% {
    top: 82%;
    opacity: 0;
  }
  74.96%,
  91.62% {
    top: 80%;
    opacity: 1;
  }
  100% {
    top: 78%;
    opacity: 0;
  }
}

.item-1 > h3,
.item-2 > h3,
.item-3 > h3 {
  all: unset;
  font-weight: 500;
  font-size: 17px;
  font-family: "Noto Sans KR", sans-serif;
  margin-right: 12px;
}

